import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl } from '@angular/forms';
import { DietCustomerDetails, Dishes, DietMealData, FinalFoods, DietDetails } from './diet-variables';
import { SetFoodService } from './set-food.service';
import { Dish, MealTypes } from '../dish-preparation/dish-interfaces';
import { element } from '@angular/core/src/render3';
import { Observable } from 'rxjs';
import { DialogueService } from 'src/app/shared/commmon/dialogue/dialogue.service';
import { MatBottomSheet } from '@angular/material';
import { PreDefinedDishComponent } from '../pre-defined-dish/pre-defined-dish.component';

@Component({
  selector: 'app-set-food',
  templateUrl: './set-food.component.html',
  styleUrls: ['./set-food.component.scss']
})
export class SetFoodComponent implements OnInit {
  CustomerID;
  DietDate = new Date();
  CustomerDetails: DietCustomerDetails = {
    Name: '',
    Gender: '',
    Age: 0,
    ChefName: '',
    FoodDietTypeName: '',
    Height: 0,
    Weight: 0,
    Waist: 0,
    WorkOutType: '',
    Frequency: 0
  };
  Foods = [];
  FilteredFood = [];
  MealTypes: DietMealData[] = [];
  DishID = 0;
  Dishes = [];
  FinalFoods: FinalFoods[] = [];

  // constructor
  constructor(
    private route: ActivatedRoute,
    private service: SetFoodService,
    private dialog: DialogueService,
    private bottomSheet: MatBottomSheet
  ) {
    this.CustomerID = parseInt(this.route.snapshot.paramMap.get('id'));
    if (this.CustomerID != null && this.CustomerID > 0) {
      this.GetDietCustomerDetails(this.CustomerID);
    }
    this.GetAllMealTypes();
    this.GetAllReadyToEatFood();

  }
  // get selected dates
  GetSelectedDietDates(){
    let AllDates=[];
    this.FinalFoods.forEach(element=>{
      AllDates.push(element.DietDate);
    });
    this.Dishes.forEach(element=>{
      AllDates.push(element.Date);
    });
    var unique = AllDates.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
    });
    return unique;
  }

  // get formatted date
  GetFormattedDate() {
    return this.DietDate.getMonth() + 1 + '/' + this.DietDate.getDate() + '/' + this.DietDate.getFullYear();
  }
  //change date to selecteddate
  ChangeDate(SelectedDate){
    this.DietDate=new Date(SelectedDate);
  }
  // get customer details
  GetDietCustomerDetails(CustomerID) {
    this.service.GetCustomerDataForDietitian(CustomerID).subscribe(Response => {
      if (Response.Status === true) {
        this.CustomerDetails = Response.Data;
      }
    });
  }
  // get all ready to eat food items
  GetAllReadyToEatFood() {
    this.service.GetAllReadyToFoods().subscribe(Response => {
      if (Response.Status === true) {
        this.Foods = Response.Data;
      }
    });
  }
  // get all meal types
  GetAllMealTypes() {
    this.service.GetAllMealTypes().subscribe(Response => {
      if (Response.Status === true) {
        this.MealTypes = Response.Data;
      }
    });
  }
  // display food name in autocomplete
  DisplayFood(food?: Dishes): string | undefined {
    return food ? food.Title : undefined;
  }
  // Add Dish
  AddDish(index) {
    const DishID = this.MealTypes[index].DishID.DishId;
    const MealTypeID = this.MealTypes[index].MealTypeId;
    const Quantity = this.MealTypes[index].Quantity;
    const DishName = this.MealTypes[index].DishID.Title;
    const SelectedDate = this.GetFormattedDate();
    if (DishName === '' || Quantity < 0 || Quantity == null || DishID === undefined || DishID == null || SelectedDate === undefined) {
      return;
    }
    this.Dishes.forEach((element, index) => {
      if (element.MealTypeID === MealTypeID && element.DishId === DishID && element.Date === SelectedDate) {
        this.Dishes.splice(index, 1);
      }
    });

    this.Dishes.push({
      MealTypeID: MealTypeID,
      DishId: DishID,
      DishName: DishName,
      Quantity: Quantity,
      Date: SelectedDate
    });
    // this.MealTypes[index].MealTypeId=0;
    // this.MealTypes[index].DishID.DishId=0;
    // this.MealTypes[index].DishID.Title="";
    // this.MealTypes[index].Quantity=0;
  }
  // filter dish to display
  FilterTempDishes(MealTypeID) {
    const SelectedDate = this.GetFormattedDate();
    return this.Dishes.filter(x => x.MealTypeID === MealTypeID && x.Date === SelectedDate);
  }
  // filter final Dishes
  FilterFinalDishes(MealTypeID) {
    const SelectedDate = this.GetFormattedDate();
    return this.FinalFoods.filter(x => x.MealTypeId === MealTypeID && x.DietDate === SelectedDate);
  }
  // remove dish from temporary dish
  RemoveTempDishes(DishID) {
    let SelectedDate=this.GetFormattedDate();
    this.Dishes.forEach((element, index) => {
      if (element.DishId === DishID&&element.Date==SelectedDate) {
        this.Dishes.splice(index, 1);
      }
    });
  }
  // add alternative group
  AddAlternatives(MealTypeId) {
    const Dishes: DietDetails[] = [];
    const Deletes = [];
    const SelectedDate = this.GetFormattedDate();

    this.Dishes.forEach((element, index) => {
      if (element.Date === SelectedDate && element.MealTypeID === MealTypeId) {
        Dishes.push({
          DishName: element.DishName,
          DishId: element.DishId,
          Quantity: element.Quantity,
          MealTypeId: MealTypeId
        });
      }
    });
    this.Dishes = this.Dishes.filter(element => {
      return !(element.Date === SelectedDate && element.MealTypeID === MealTypeId);
    });

    if (Dishes.length > 0) {
      let id = this.FinalFoods.length;
      this.FinalFoods.push({
        Id: id++,
        DietDate: SelectedDate,
        MealTypeId: MealTypeId,
        CustomerId: this.CustomerID,
        CustomerdietDetails: Dishes
      });
    }
  }
  // save diet
  SaveDiet() {
    if (this.Dishes.length > 0) {
      this.MealTypes.forEach(element => {
        this.AddAlternatives(element.MealTypeId);
      });
    }
    this.service.SaveCustomerDiet(this.FinalFoods).subscribe(Response => {
      if (Response.Status === true) {
        this.dialog.SetData({
          Message: 'Data saved successfully',
          Route: '/ab/diet',
          Status: ''
        });
      } else {
        this.dialog.SetData({
          Message: 'Something went wrong',
          Route: '/ab/diet',
          Status: ''
        });
      }
    });
  }
  // delete final dish
  DeleteFinalDish(ID) {
    this.FinalFoods.forEach((element, index) => {
      if (element.Id === ID) {
        this.FinalFoods.splice(index, 1);
      }
    });
  }
  // filter foods
  onSearchChange(searchValue: string) {
    this.FilteredFood = [];
    this.FilteredFood = this.Foods.filter(x => x.Title.toLowerCase().includes(searchValue.toLowerCase()));
  }
  // open dish bottom sheet
  openBottomSheet(MealTypeID): void {
    this.MealTypes.forEach(element => {
      if (element.MealTypeId === MealTypeID) {
        const BottomSheetRef = this.bottomSheet.open(PreDefinedDishComponent, {
          data: { MealTypeID: MealTypeID, MealTypeName: element.MealTypeName }
        });
        BottomSheetRef.afterDismissed().subscribe(Response => {
          if(Response) {
            this.AddPredefinedDish(Response);
          }
        });
      }
    });
  }
  // add predefined dish
  AddPredefinedDish(Response) {
    const SelectedDate = this.GetFormattedDate();
    this.Dishes.forEach((element, index) => {
      if (element.MealTypeID === Response.MealTypeID && element.DishId === Response.DishID && element.Date === SelectedDate) {
        this.Dishes.splice(index, 1);
      }
    });
    this.Dishes.push({
      MealTypeID: Response.MealTypeID,
      DishId: Response.DishID,
      DishName: Response.Title,
      Quantity: 1,
      Date: SelectedDate
    });
  }
  ngOnInit(): void {}
}
